

.Navbar{
    width: 90%;
    height: 8%;
    /* background-color: rgb(18,19,22,0.85); */
    margin: auto;
    border-radius: 5vw;
    /* box-shadow: 0.1px 0.1px 2vw rgb(27, 27, 27); */
    translate: 5vw 3vh;
    position: fixed;
    animation-duration: 1s;
    transition: all 0.3s ease;
    z-index: 1;
}
.Logo{
    width: 40%;
}
.Logo img{
    width: 7vw;
    translate: 1vw 0.5vh;
    transition: all 0.3s ease;
}

.Navbar > div {
    float: left;
}

.Links {
    width: 40%;
}

.Links > div {
    float: left;
    padding-left: 3vw;
    padding-top: 2vh;
    padding-bottom: 3vh;
}

.Links > div > a{
    color: rgb(153,153,153);
    text-decoration: none;
    font-size: 2vh;
    transition: all 0.3s ease;
}

.Links  > div > a:hover{
    color: rgb(211, 211, 211);
    transition: all 0.3s ease;
}

.Buttons{
    width: 20%;
    translate: 2vw 0.5vh;
}


.Buttons div{
    float: left;
    text-align: center;
    height: 4vh;
    border-radius: 2vw ;
    padding-top: 1vh;
}


.Buttons div a{
    font-size: 2vh;
}

.Login{
    background-color: rgb(51,51,51);
    border: 1px solid rgb(128,128,128);
    margin-top: 5px;
    width: 6vw;

}
.Login a{
    color: rgb(153,153,153);
    text-decoration: none;
}

.GetStarted{
    background-color: rgb(77,77,77);
    margin-top: 5px;
    width: 8vw;
    margin-left: 1vw;
    transition: all 1s ease;

}
.GetStarted a{
    color: white;
    text-decoration: none;
}

    /* Media Queries */
    @media (max-width: 768px) {
    .Navbar{
        width: 90%;
        height: 8%;
        /* background-color: rgb(18,19,22,0.85); */
        margin: auto;
        border-radius: 5vw;
        /* box-shadow: 0.1px 0.1px 2vw rgb(27, 27, 27); */
        translate: 3vw 2vh;
        position: fixed;
        animation-duration: 1s;
        transition: all 0.3s ease;
        z-index: 1;
    }
    .Logo img{
        width: 40vw;
        translate: 3vw 2vh;
        transition: all 0.3s ease;
    }
    .Links,
    .Buttons {
      display: none;
    }
}